











import {Vue, Component} from 'vue-property-decorator'

const myMixin = {
    created () {
        this.hello()
    },
    methods: {
        hello () {
            this.$notify({
                title: 'hello',
                message: 'hi'
            })
        }
    }
}

@Component({
    mixins: [myMixin]
})
export default class Basics extends Vue {
}
