


























import {Vue, Component} from 'vue-property-decorator'

const myMixin = {
    data () {
        return {
            message: 'hello',
            foo: 'mixins'
        }
    },
    created () {
        this.hello()
        this.notify('Mixins', '先执行')
    },
    methods: {
        notify (title, message) {
            setTimeout(() => {
                this.$notify({
                    title,
                    message
                })
            })
        },
        hello () {
            this.notify(this.foo, this.message)
        }
    }
}

@Component({
    mixins: [myMixin]
})
export default class OptionMerging extends Vue {
    message = 'hello'
    foo = 'self'

    created() {
        this.notify('self', '后执行')
    }
}
