module.exports = "<template>\r\n    <div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>1. 基础</h4>\r\n            <el-button @click=\"hello\">mixins</el-button>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component} from 'vue-property-decorator'\r\n\r\n    const myMixin = {\r\n        created () {\r\n            this.hello()\r\n        },\r\n        methods: {\r\n            hello () {\r\n                this.$notify({\r\n                    title: 'hello',\r\n                    message: 'hi'\r\n                })\r\n            }\r\n        }\r\n    }\r\n\r\n    @Component({\r\n        mixins: [myMixin]\r\n    })\r\n    export default class Basics extends Vue {\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";